import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'
import Link from 'gatsby-link'
import {graphql} from "gatsby"

import '../styles/course.scss'

export default function Template({data}) {
    const {LessonsQuery: lessons} = data
    const {CourseQuery: course} = data
    const pageTitle = course.frontmatter.title

    function str_pad_left(string, pad, length) {
        return (new Array(length+1).join(pad)+string).slice(-length);
    }

    return (
        <Layout>
            <SEO title={pageTitle} />
            <div className="courses-header pb-5 pt-5">
                <div className="container">
                    <h1 className="mb-0">{pageTitle}</h1>
                </div>
            </div>
            <div className="container mb-0 mt-5">
                <div dangerouslySetInnerHTML={{__html: course.html}} />
            </div>
            <div className="container mb-5 mt-0">
                <h2>Lessons</h2>
                {lessons.edges.map(function(lesson) {
                    const lessonTimeSeconds = lesson.node.frontmatter.time
                    const minutes = Math.floor(lessonTimeSeconds / 60)
                    const seconds = lessonTimeSeconds - minutes * 60
                    const lessonTime = str_pad_left(minutes,'0',2)+'m ' + ((seconds > 0) ? seconds + 's' : '')

                    return (
                        <div className="row lesson-container-line">
                            <div className="col-md-1 timeline-animation">
                                <div className="step-icon">
                                    <span className="color-white lesson-number">{lesson.node.frontmatter.order_number}</span>
                                </div>
                            </div>
                            <div className="col-md-11 lesson-title">
                                <div key={lesson.node.id} className="video-container mb-3 lesson-line">
                                    <Link to={lesson.node.frontmatter.path}>
                                        <h4>{lesson.node.frontmatter.title}</h4>
                                    </Link>
                                    <div className="row ml-0 timing">
                                        <span className="material-icons mr-1">access_time</span>
                                        <div>{lessonTime}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </Layout>
    )
}

export const pageQuery = graphql`
  query($course: String!) {
    LessonsQuery: allMarkdownRemark(
      filter: { 
        frontmatter: {
          course: {eq: $course},
          type: {eq: "lesson"},
          active: {eq: 1}
        }
      },
      sort: {
        fields: [frontmatter___order],
        order: ASC
      }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            time
            order_number
          }
        }
      }
    }
    
    CourseQuery: markdownRemark(
      frontmatter: {
        code: {
          eq: $course 
        }
      }
    ) {
      id
      html
      frontmatter {
        title
      }
    }
  }
`
